<template>
  <div id="map-container">
    <div class="gray-layer"></div>

    <yandex-map
      zoom="13"
      :coords="mapOption.coords"
      :controls="mapOption.controls"
      style="width: 100%; height: 100%;"
      @map-was-initialized="init"
    >
      <ymap-marker
        :coords="mapOption.coords"
        marker-type="circle"
        circle-radius="100"
        marker-id="0"
      />
      <ymap-marker
        v-for="(marker, index) in markers"
        :key="index + 1"
        :marker-id="index + 1"
        :marker-fill="fill"
        :marker-stroke="stroke"
        :coords="marker.coords"
        :hint-content="marker.hintContent"
        marker-type="circle"
        circle-radius="75"
      />
    </yandex-map>
  </div>
</template>

<script>
export default {
  name: 'TheMap',
  data: () => ({
    fill: {
      color: '#000000',
      opacity: 0.4,
    },
    stroke: {
      color: '#ff0000',
      width: 1,
    },
  }),
  computed: {
    mapOption() {
      return this.$store.getters.getMapOption;
    },
    markers() {
      return this.$store.getters.getMapMarkers;
    },
  },
  methods: {
    initNew() {
      const multiRoute = new ymaps.multiRouter.MultiRoute({
        // Описание опорных точек мультимаршрута.
        referencePoints: [
            [55.734876, 37.59308],
            'Москва, ул. Мясницкая'
        ],
        // Параметры маршрутизации.
        params: {
            // Ограничение на максимальное количество маршрутов, возвращаемое маршрутизатором.
            results: 2
        }
      }, {
          // Автоматически устанавливать границы карты так, чтобы маршрут был виден целиком.
          boundsAutoApply: true
      });

      myMap.geoObjects.add(multiRoute);
    },
    init() {
      // Добавим на карту схему проезда
      // от улицы Крылатские холмы до станции метро "Кунцевская"
      // через станцию "Молодежная" и затем до станции "Пионерская".
      // Точки маршрута можно задавать 3 способами:
      // как строка, как объект или как массив геокоординат.
      ymaps.route([
          'Москва, улица Крылатские холмы',
          {
              point: 'Москва, метро Молодежная',
              // метро "Молодежная" - транзитная точка
              // (проезжать через эту точку, но не останавливаться в ней).
              type: 'viaPoint'
          },
          [55.731272, 37.447198], // метро "Кунцевская".
          'Москва, метро Пионерская'
      ]).then(function (route) {
          myMap.geoObjects.add(route);
          // Зададим содержание иконок начальной и конечной точкам маршрута.
          // С помощью метода getWayPoints() получаем массив точек маршрута.
          // Массив транзитных точек маршрута можно получить с помощью метода getViaPoints.
          var points = route.getWayPoints(),
              lastPoint = points.getLength() - 1;
          // Задаем стиль метки - иконки будут красного цвета, и
          // их изображения будут растягиваться под контент.
          points.options.set('preset', 'islands#redStretchyIcon');
          // Задаем контент меток в начальной и конечной точках.
          points.get(0).properties.set('iconContent', 'Точка отправления');
          points.get(lastPoint).properties.set('iconContent', 'Точка прибытия');

          // Проанализируем маршрут по сегментам.
          // Сегмент - участок маршрута, который нужно проехать до следующего
          // изменения направления движения.
          // Для того, чтобы получить сегменты маршрута, сначала необходимо получить
          // отдельно каждый путь маршрута.
          // Весь маршрут делится на два пути:
          // 1) от улицы Крылатские холмы до станции "Кунцевская";
          // 2) от станции "Кунцевская" до "Пионерская".

          var moveList = 'Трогаемся,</br>',
              way,
              segments;
          // Получаем массив путей.
          for (var i = 0; i < route.getPaths().getLength(); i++) {
              way = route.getPaths().get(i);
              segments = way.getSegments();
              for (var j = 0; j < segments.length; j++) {
                  var street = segments[j].getStreet();
                  moveList += ('Едем ' + segments[j].getHumanAction() + (street ? ' на ' + street : '') + ', проезжаем ' + segments[j].getLength() + ' м.,');
                  moveList += '</br>'
              }
          }
          moveList += 'Останавливаемся.';
          // Выводим маршрутный лист.
          $('#list').append(moveList);
      }, function (error) {
          alert('Возникла ошибка: ' + error.message);
      });
    },
  }
};
</script>

<style scoped>
#map-container {
  width: 70%;
  height: 100vh;
}
#map-container > div {
  width: 100%;
  height: 100%;
}

.gray-layer {
  position: absolute;
}
</style>

